import React, { useState } from 'react';
import { postDataAPI } from '../../utils/fetchData';
import { useSelector } from 'react-redux';
//import { v4 as uuidv4 } from 'uuid'; // Importar la funcin v4 de uuid para generar un UUID

const Confirmacioncorreo = () => {
    const { auth } = useSelector((state) => state);
    const [correoDestino, setCorreoDestino] = useState('');
    const [loading, setLoading] = useState(false);

    const enviarCorreoConfirmacion = async () => {
        try {
            setLoading(true);
            // Generar un token UUID nico
          //  const token = uuidv4();

            // Enviar solicitud para enviar correo electrnico de confirmacin
            const respuesta = await postDataAPI('enviarcorreoconfirmacion', {
                correoDestino: correoDestino, // Usar el valor del campo de entrada
              //  token: token, // Incluir el token UUID en la solicitud
            }, auth.token);

            if (respuesta.ok) {
                alert('Correo electrnico de confirmacin enviado correctamente. Por favor, verifica tu correo electrnico.');
            } else {
                 
                alert('Hubo un error al enviar el correo electrnico de confirmacin. Por favor, intntalo de nuevo ms tarde.');
            }
        } catch (error) {
             
            alert('Hubo un error al enviar el correo electrnico de confirmacin. Por favor, intntalo de nuevo ms tarde.');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div>
            <input
                type="email"
                placeholder="Correo electrnico de destino"
                value={correoDestino}
                onChange={(e) => setCorreoDestino(e.target.value)}
            />

            <button onClick={enviarCorreoConfirmacion} disabled={loading}>
                {loading ? 'Enviando...' : 'Enviar Correo de Confirmacin'}
            </button>
        </div>
    );
};

export default Confirmacioncorreo;
